.donatemain {
  display: flex;
  padding: 3rem;
  padding-left: 9rem;
  padding-bottom: 3rem;
  justify-content: center;
  font-size: 1.5rem;
}

.donate-img {
  float: left;
  padding-right: 5rem;
  width: 19rem;
  height: 18rem;
  object-fit: cover;
}
img-section {
  display: flex;
}
.doante-content {
  text-align: left;
}
.donate-form {
  border: 1px solid black;
  padding: 2rem;
  box-shadow: 2px 2px 2px 2px gray;
  border: none;
}
.Donate-btn {
  margin-left: 16rem;
  padding: 1rem;
  background-color: #4c1105;
  font-weight: 600;
  font-size: 1.3rem;
  color: white;
  border-radius: 3px;
  border: none;
  box-shadow: inset;
}
.Donate-btn:hover {
  background-color: rgb(255, 162, 162);
}
.messagebox {
  height: 9rem;
}
.donate-level p {
  font-size: 5px;
  color: gray;
}
.donate-icon {
  width: 2rem;
  font-size: 1.3rem;
  color: rgb(154 52 18);
}
.messagebox {
  font-size: 8px;
  color: black;
  font-weight: 200;
}
.donatemain p {
  font-size: 1.2rem;
}
