/* Main component */
.main {
  padding: 7rem;
  text-align: center;
  background-color: #4c1105;
  background-image: linear-gradient(
      rgba(34, 34, 34, 0.6),
      rgba(34, 34, 34, 0.6)
    ),
    url('https://images.unsplash.com/photo-1615461066841-6116e61058f4?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=983&q=80');
  background-repeat: no-repeat;
  background-size: 100%;
  color: #fff;
  height: 89.5vh;
}

.main-title {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 5rem;
  /* width: 60rem; */
  margin: auto;
}

.border-line {
  display: block;
  border-bottom: 3px solid#fff;
  width: 58rem;
  margin: 0 0 10rem 0;
}

.main h1 {
  font-size: 5rem;
  padding: 0;
}

.title-tag {
  font-size: 1.7rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.hands-helping {
  font-size: 3rem;
}

.main-need {
  margin-bottom: 5rem;
}

.main-need p {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  letter-spacing: 2px;
}

.main-donate p {
  font-size: 2.5rem;
  letter-spacing: 2px;
  margin: 10rem 0 2rem 0;
}

.main .button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  width: 20rem;
  padding: 1rem;
  background-color: rgb(255, 162, 162);
  border: none;
  border-radius: 10px;
  color: rgb(142, 1, 1);
  font-weight: 700;
  letter-spacing: 1.5px;
  font-size: 2rem;
  transition: all 0.5s ease-out;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}

.btn:hover {
  cursor: pointer;
  background-position: left bottom;
}

.btn-need {
  background: linear-gradient(
    to right,
    rgb(173, 61, 39) 50%,
    rgb(255, 162, 162) 50%
  );
  background-size: 200% 100%;
  background-position: right bottom;
}

.btn-need:hover {
  background-color: rgb(173, 61, 39);
  color: #fff;
  box-shadow: inset 0 0 0 1px rgb(255, 162, 162);
}

.btn-donate {
  background: linear-gradient(to right, rgb(255, 162, 162) 50%, white 50%);
  background-size: 200% 100%;
  background-position: left bottom;
}

.btn-donate:hover {
  background-color: #fff;
  box-shadow: 0 0 5px #fff;
  background-position: right bottom;
}
