.accordion {
  display: grid;
  /* grid-template-columns: 1fr 2fr; */
  background-color: #fff;
  max-width: 60vw;
  margin: 5rem auto;
  padding: 5rem 3rem;
  border-radius: 5px;
  height: 80vh;
}

.accordion-heading {
  font-size: 3rem;
  font-weight: 500;
  margin-bottom: 5rem;
}

.section--accordions {
  display: flex;
  flex-direction: column;
}

.ac-main {
  align-items: center;
  margin-bottom: 2rem;
  border-radius: 2px;
  box-shadow: 0 0 5px #bbb;
  padding: 2rem;
}

.title-button {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-size: 1.6rem;
}

.acbtn {
  width: 2rem;
  height: 2rem;
  background-color: #eae6eb;
  border-radius: 50%;
  color: rgb(174, 53, 53);
  min-width: 2rem;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
}

.acbtn:hover {
  cursor: pointer;
}

.info {
  font-size: 1.5rem;
  color: #555;
}
